</template>

<script>
export default {
  name: 'TemporaryMsg',
  data() {
    return {
		cvLink: 'https://drive.google.com/file/d/1W5ek471-Fx29QZLzW9-1l_YAj27KMuSJ/view?usp=sharing'
	}
  }
}
</script>

<style lang="scss" scoped>
    .temp {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;

		> div {
		width: 100%;
		}
    }
</style>


