<template>
  <div id="app">
    <div class="contain">
      <aside class="sidebar contain__sidebar">
        <app-logo></app-logo>
        <app-navigation></app-navigation>

        <app-footer></app-footer>
      </aside>
      

      <section class="page contain__page">
        <router-view></router-view>        
      </section>
    </div>

    <!-- {{ 'Browser: ' + this.getCurrentBrowser() }} <br/>
    {{ 'Device type: ' + this.getDeviceType() }} <br/>
    {{ 'Operating System: ' + this.getOperatingSystem() }} <br/> -->
  </div>
</template>

<script>
import AppLogo from '@/components/modules/AppLogo'
import AppFooter from '@/components/modules/AppFooter'
import AppNavigation from '@/components/modules/AppNavigation'

let check = key => {
  const UA = navigator.userAgent
  
  return key instanceof RegExp ? UA.match(key) : UA.indexOf(key) != -1
}

export default {
  name: 'App',
  components: { AppLogo, AppNavigation, AppFooter },

  data() {
    return {}
  },

  created() {
    console.log(this.getOperatingSystem())
    console.log(this)
  },

  methods: {
    getCurrentBrowser() {
      const browser = {
        InternetExplorer: () => check('; MSIE') || check('Trident/') || check('.*rv:'),
        Edge:             () => check('Edge'),
        Seamonkey:        () => check('Seamonkey/'),
        Firefox:          () => check('Firefox/') && !browser.Seamonkey(),
        EdgeChromium:     () => check('Edg/'),
        Chromium:         () => check('Chromium/'),
        Chrome:           () => check('Chrome/') && !(browser.Chromium() || browser.EdgeChromium() || browser.Edge()),
        Safari:           () => check('Safari/') && !check('Chrome/'),
        Opera:            () => check('OPR/') || check('Opera/')
      }

      for(let i in browser) {
        if(browser[i]()) return i
      }
    },

    getDeviceType() {
      const mobile = {
        Android:    () => check(/Android/i),
        BlackBerry: () => check(/BlackBerry/i),
        iOS:        () => check(/iPhone|iPad|iPod/i),
        Opera:      () => check(/Opera Mini/i),
        Windows:    () => check(/IEMobile/i) || check(/WPDesktop/i),
        any:        () => mobile.Android() || mobile.BlackBerry() || mobile.iOS() || mobile.Opera() || mobile.Windows()
      }

      return mobile.any() ? 'Mobile' : 'Desktop'
    },

    getOperatingSystem() {
      const operatingSystem = {
        'Windows Phone': () => check('Windows Phone'),
        'Android':       () => check('Android'),
        'iOS':           () => check(/iPhone|iPad|iPod/i),
        'Macintosh':     () => check('Mac'),
        'Windows 2000':  () => check('Windows NT 5.0'),
        'Windows XP':    () => check('Windows NT 5.1'),
        'Windows Vista': () => check('Windows NT 6.0'),
        'Windows 7':     () => check('Windows NT 6.1'),
        'Windows 8':     () => check('Windows NT 6.2'),
        'Windows 10':    () => check('Windows NT 10.0'),
        'UNIX':          () => check('X11'),
        'Linux':         () => check('Linux'),
        'BlackBerry OS': () => check('BB') || check('PlayBook'),
        'Kai OS':        () => check('KAIOS')
      }

      for(let i in operatingSystem) {
        if(operatingSystem[i]()) return i
      }
    }
  }
}
</script>














