<template>
    <nav class="navigation">
        <ul>
            <li v-for="nav in navigations"
                :key="nav"
                :class="{ active: nav == currentPage || (nav == 'about' && currentPage == 'index')}"
            >
                <router-link :to="nav != 'about' ? nav : '/'">
                    {{ nav }} 
                </router-link>
                <span> -> </span>
            </li>
        </ul>
    </nav>
</template>

<script>
export default {
  data() {
    return {
      navigations: ['about', 'showcase', 'contact'],
    }
  },

  computed: {
    currentPage() {
      return this.$route.name;
    }
  }
}
</script>


